import React, { Component } from 'react'
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'

export default class DemoAlert extends Component {
  render() {
    return (
      <div className="position-absolute start-0 bottom-0 w-100 bg-light" style={{zIndex:99}}>
        <div className="d-flex fade mb-0 border-top rounded-0 border-warning  alert alert-warning show w-100">
            <FontAwesomeIcon icon={'info'} iconClass="fa-solid fa-circle-info fs-6 lh-sm text-warning me-2" /> 
            This is Demo Data For Your Reference.
        </div>
        </div>
    )
  }
}
