import { useEffect, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { css as codeCSS } from "@codemirror/lang-css"
import { Accordion } from "react-bootstrap";
import BackgroundStyle from "./BackgroundStyle";
import { useDispatch, useSelector } from "react-redux";
import { updateCutomCSS } from "../../../../../actions/appsMoreAction";
import ThemeSetting from "./ThemeSetting";
import UGCUploads from "./UGCUploads";
import CardStyle from "./CardStyle";
import { TOAST_UPDATE, TOAST_LOADING } from "../../../../../utils";
import { ACTIVE_VIDEO_POPUP, GET_CUSTOM_CSS_STATUS } from "../../../../../reducer/keys";
import { SET_CUSTOM_CSS } from "../../../../../reducer/keys";
import GoogleAnalytics from "./GoogleAnalytic";
import ErrorFound from "../../../../ErrorFound";
import CssSwitch from "./Element/CssSwitch";
import HeaderStyle from "./HeaderStyle";


const Customization = (props) => {
  const { openBanner, a_key, bannerDetail, appData, fonts, userData, onCtaChange, setBanner } = props
  const getCtastatus = useSelector((state) => state.appsMore);

  const [activeKey, setActiveKey] = useState('banner')
  const [cssOn, setcssOn] = useState(false)
  const dispatch = useDispatch()

  let backgroundDetails = appData?.wall;
  let onsite = appData?.wall?.UgcSettings;
  let PersonalizationID = appData?.wall?.Personalization?.id
  let onsiteStatus = appData

  useEffect(() => {

    dispatch({ type: ACTIVE_VIDEO_POPUP, payload: 'banner' })

    window.$("#wallThemeRuleCtaStatus").on('change', function (e) {
      onCtaChange()
    })

    window.$("#wallcssStatus").on('change', function (e) {
      setcssOn(this.value == 'true' ? true : false)
    })
  }, [cssOn])

  const applyCss = () => {
    const toastId = TOAST_LOADING()
    updateCutomCSS(appData?.wall?.Personalization.css, PersonalizationID).then(res => {
      const { data } = res;
      if (data.status) {
        TOAST_UPDATE(toastId, "success", data.message)
        // TOAST_SUCCESS(data.message)
      }
      else TOAST_UPDATE(toastId, "error", data.message)
      // openBanner()
    }
    ).catch(err => {
      console.log('err', err)
      TOAST_UPDATE(toastId, "error", "Error in updating")
    })
  }

  useEffect(() => {
    if (appData?.save_and_publish == true && appData?.is_custome_css_change == true) applyCss();
  }, [appData?.save_and_publish]);

  return (<Accordion className="border-top" defaultActiveKey="background" flush>
    <Accordion.Item eventKey="background">
      <Accordion.Header>Background</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound>
          <BackgroundStyle backgroundDetails={backgroundDetails} save_and_publish={appData?.save_and_publish} is_backgroun_change={appData?.is_backgroun_change} PersonalizationID={PersonalizationID} bannerDetail={appData.wall} />
        </ErrorFound>
      </Accordion.Body>
    </Accordion.Item>
    {[63,64].includes(appData?.wall?.Personalization?.commerceTheme) ? <Accordion.Item eventKey="header_style">
      <Accordion.Header>Header Style</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        {fonts && fonts.length && appData?.wall && Object.keys(appData).length > 0 ?
          <ErrorFound><HeaderStyle save_and_publish={appData?.save_and_publish} bannerDetail={appData?.wall} ownerId={appData?.wallOwner} fonts={fonts} planID={appData?.wall?.UserPlan?.planId} /></ErrorFound>
          : null}
      </Accordion.Body>
    </Accordion.Item> : null}
    <Accordion.Item eventKey="card_style">
      <Accordion.Header>Card Style</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        {fonts && fonts.length && appData?.wall && Object.keys(appData).length > 0 ?
          <ErrorFound><CardStyle save_and_publish={appData?.save_and_publish} bannerDetail={appData?.wall} ownerId={appData?.wallOwner} fonts={fonts} planID={appData?.wall?.UserPlan?.planId} /></ErrorFound>
          : null}
      </Accordion.Body>
    </Accordion.Item>
    <Accordion.Item eventKey="more_actions">
      <Accordion.Header>More Actions</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound> <ThemeSetting planID={appData?.wall?.UserPlan?.planId} appData={appData} save_and_publish={appData?.save_and_publish} is_theme_setting_change={appData?.is_theme_setting_change} bannerDetail={appData?.wall} db_table={appData?.wall?.UserPlan?.db_table} adminId={appData.wall} userData={userData} closePopup={openBanner} /></ErrorFound>
      </Accordion.Body>
    </Accordion.Item>
    {(appData?.wall?.UserRule?.customCss != 0) ?
      <Accordion.Item eventKey={a_key ? a_key : activeKey}>
        <Accordion.Header>Custom CSS</Accordion.Header>
        <Accordion.Body className="bg-gray-100">
          <CssSwitch />
          <div className={`${appData?.wall?.Personalization?.cssStatus ? '' : 'inActiveCustomeCss'}`} >
            <ErrorFound> <CodeMirror value={appData?.wall?.Personalization.css} height="350px" save_and_publish={appData?.save_and_publish} basicSetup={{ foldGutter: false, dropCursor: false, allowMultipleSelections: false, indentOnInput: false, autocompletion: false }}  extensions={[codeCSS()]} onChange={(e) => { dispatch({ type: SET_CUSTOM_CSS, payload: e }) }} editable={appData?.wall?.Personalization?.cssStatus ? true : false} /> </ErrorFound>
          </div>
        </Accordion.Body>
      </Accordion.Item> : null}
    <Accordion.Item eventKey="google_analytics">
      <Accordion.Header>Google Analytics</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound><GoogleAnalytics getData={bannerDetail} save_and_publish={appData?.save_and_publish} is_utm_tracker_change={appData?.is_utm_tracker_change} /></ErrorFound>
      </Accordion.Body>
    </Accordion.Item>
    {appData.onsite_status && !([63,64].includes(appData?.wall?.Personalization?.commerceTheme))?
      <Accordion.Item eventKey="snap_up">
        <Accordion.Header>Review Hub settings</Accordion.Header>
        <Accordion.Body className="bg-gray-100">
          <ErrorFound><UGCUploads ugcDetails={onsite} save_and_publish={appData?.save_and_publish} is_magic_upload_change={appData?.is_magic_upload_change} PersonalizationID={PersonalizationID} /></ErrorFound>
        </Accordion.Body>
      </Accordion.Item> : null}
  </Accordion>
  );
};




export default Customization;