import { Component } from "react";
import ErrorFound from "../../../../ErrorFound";
import InheritSwitch from "./Element/InheritSwitch";
import SelectFont from "../Components/SelectFont";
import { Form } from "react-bootstrap";
import ColorPicker from "../../CommonComponents/ColorPicker";
import RangeInput from "../../CommonComponents/RangeInput";
import { connect } from "react-redux";
import { HDR_BTN_COLOR, HDR_BTN_FONT, HDR_BTN_FONT_COLOR, HDR_BTN_FONT_SIZE, HDR_BTN_TEXT, HDR_BTN_VARIANT, HDR_COLOR, HDR_FONT, HDR_FONT_SIZE, HDR_STAR_COLOR, HDR_TEXT, HDR_VARIANT } from "../../../../../reducer/keys";
import { updateHeader } from "../../../../../actions/appsMoreAction";
import { onSiteUpload } from "../../../../../actions/customization";

class HeaderStyle extends Component{

    onChangeVariant = (e,val) => {
        const {hdrVariant, btnVariant} = this.props
        if(val == 'btn') btnVariant(e.label.key)
        else hdrVariant(e.label.key)
    }


    componentDidUpdate(prevProps) {
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
            if (this.props.isHeaderUpdate) {
            setTimeout(() => {
                this.onSubmitData();
            }, 500);
            }
        }
    }

    onSubmitData = (e) => {

        const { bannerDetail } = this.props;
        const { UgcSettings } = bannerDetail
        const data = {
            headerInherit:UgcSettings.headerInherit,
            headerFont:UgcSettings.headerFont.includes('+') ? UgcSettings.headerFont.replace('+',' ') : UgcSettings.headerFont,
            headerFontLink: UgcSettings.headerFont,
            headerFontVariant:UgcSettings.headerFontVariant,
            headerFontColor:UgcSettings.headerFontColor,
            headingText:UgcSettings.headingText,
            headerFontSize:UgcSettings.headerFontSize,
            buttonFontFamily:UgcSettings.buttonFontFamily.includes('+') ? UgcSettings.buttonFontFamily.replace('+',' ') : UgcSettings.buttonFontFamily,
            buttonFontLink: UgcSettings.buttonFontFamily,
            buttonFontWeight:UgcSettings.buttonFontWeight,
            buttonTextColor:UgcSettings.buttonTextColor,
            buttonColor:UgcSettings.buttonColor,
            buttonText:UgcSettings.buttonText,
            buttonFontSize:UgcSettings.buttonFontSize,
            ratingColor:UgcSettings.ratingColor,
            wallId: bannerDetail.Wall.wallId ? bannerDetail.Wall.wallId : localStorage.getItem('active_wall'),
            themeId: bannerDetail.ThemeRule.themeId,
            websiteId: bannerDetail.Wall?.id || localStorage.getItem('website_id'),
            id: bannerDetail.Personalization.id
        }
    
        onSiteUpload(data, true).then(res => {
          this.setState({ spinner: false })
        }).catch((error) => console.warn(error))
    
    }

    onFontChange = (event, val) => {
        const { allFont, bannerDetail, hdrFont, btnFont } = this.props
        const { UgcSettings } = bannerDetail
        const selectedFont = allFont.find(font => font.family == event.label.key)
        const variant = val == 'btn' ? UgcSettings.buttonFontWeight : UgcSettings.headerFontVariant
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == variant)
        if(val == 'btn') btnFont(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
        else hdrFont(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    }

    render(){
        const { inheritStyle, bannerDetail } = this.props;
        const {UgcSettings} = bannerDetail
        return <>
            <ErrorFound><InheritSwitch/></ErrorFound>
            <div>
                <p className="h5">Heading</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_t">Header Text</Form.Label>
                <Form.Control id="h_t" value={UgcSettings?.headingText} maxLength="30" onChange={e => { this.props.hdrText(e.target.value) }} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={this.onFontChange} variantChange={this.onChangeVariant} fontval={UgcSettings?.headerFont} vrntVal={UgcSettings?.headerFontVariant} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_c">Font Color</Form.Label>
                <ColorPicker id="h_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={UgcSettings?.headerFontColor} changeColor={(clr) => this.props.hdrColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_s">Font Size</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="h_s" min={10} max={50} step={1} defaultValue={UgcSettings?.headerFontSize} value={UgcSettings?.headerFontSize} onChange={(event) => { this.props.hdrSize(event) }} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_c">Star Color</Form.Label>
                <ColorPicker id="s_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={UgcSettings?.ratingColor} changeColor={(clr) => this.props.hdrStar(clr)} inheritStyle={inheritStyle} />
            </div>
            <hr className=""></hr>
            <div>
                <p className="h5">Review Button</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_t">Button Text</Form.Label>
                <Form.Control id="b_t" value={UgcSettings?.buttonText} maxLength="30" onChange={e => { this.props.btnText(e.target.value) }} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={event=>this.onFontChange(event, 'btn')} variantChange={event=> this.onChangeVariant(event, 'btn')} fontval={UgcSettings?.buttonFontFamily} vrntVal={UgcSettings?.buttonFontWeight} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_c">Font Color</Form.Label>
                <ColorPicker id="b_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={UgcSettings?.buttonTextColor} changeColor={(clr) => this.props.btnFontColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_bgc">Background Color</Form.Label>
                <ColorPicker id="b_bgc" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={UgcSettings?.buttonColor} changeColor={(clr) => this.props.btnColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_s">Font Size</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="b_s" min={10} max={50} step={1} defaultValue={UgcSettings?.buttonFontSize} value={UgcSettings?.buttonFontSize} onChange={(event) => { this.props.btnSize(event) }} />
            </div>
            

        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allFont: state.allFont?.allFonts,
        isHeaderUpdate: state.appData?.isHeaderUpdate,
        inheritStyle: state.appData?.wall?.ThemeRule?.inheritStyles,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        hdrFont: (font, variant) => dispatch({type:HDR_FONT, font:font, variant:variant}),
        hdrVariant: (val) => dispatch({type:HDR_VARIANT, payload:val}),
        hdrColor: (val) => dispatch({type:HDR_COLOR, payload:val}),
        hdrSize: (val) => dispatch({type:HDR_FONT_SIZE, payload:val}),
        hdrText:(val) => dispatch({type:HDR_TEXT, payload:val}),
        hdrStar:(val)=>dispatch({type:HDR_STAR_COLOR, payload:val}),
        btnText:(val) => dispatch({type:HDR_BTN_TEXT, payload:val}),
        btnFont: (font, variant) => dispatch({type:HDR_BTN_FONT, font:font, variant:variant}),
        btnVariant: (val) => dispatch({type:HDR_BTN_VARIANT, payload:val}),
        btnFontColor: (val) => dispatch({type:HDR_BTN_FONT_COLOR, payload:val}),
        btnColor: (val) => dispatch({type:HDR_BTN_COLOR, payload:val}),
        btnSize: (val) => dispatch({type:HDR_BTN_FONT_SIZE, payload:val}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HeaderStyle)