import Swal from "sweetalert2";
import store from "../store";
import { SAVE_AND_PUBLISH, SET_ALL_UPDATED } from "../reducer/keys";


export const isWebsiteChanged = (callback, skip) => {
    
    const appData = store.getState().appData
    const websiteData = store.getState().websiteList
    if(skip) callback()
    else {
        if ((appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change || appData?.is_wall_change || appData?.isHeaderUpdate) && websiteData?.websiteList.length > 0) {
            Swal.fire({
                title: "Changes not saved!",
                icon: "warning",
                showCancelButton: true,
                confirmButtonText: "Save & Continue",
                cancelButtonText: "Discard",
            }).then(res => {
                if (res.dismiss != "backdrop") {
                    if (res.isConfirmed) {
                        const ele = document.querySelector('#saveSetting');
                        if(ele) ele.click()
                        else store.dispatch({ type: SAVE_AND_PUBLISH, payload: true })
                        store.dispatch({ type: SET_ALL_UPDATED, payload: false })
                        setTimeout(() => {
                            callback()
                        }, 500);
                    }
                    else DiscardSettings(callback)
                }
            })
        }
        else if (websiteData?.websiteList.length <= 0){
            store.dispatch({ type: SET_ALL_UPDATED, payload: false })
            callback()
        } 
        else callback()
    }
    
};

const DiscardSettings = (callback) => {
    const element = document.querySelector('#discardSetting');
    if(element){
        element.setAttribute('data-nav','nav-click')
        element.click()
    }
    callback();
};
