import React, { Component } from 'react'
import { connect } from 'react-redux'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { Button, Row, Col, Image, Nav, Tab, Form } from 'react-bootstrap'
import { APPLY_THEME_ACTION, CHANGE_THEME_ACTION } from '../../../../../actions/themeAction'
import { checkPermission } from '../../../../../actions/collaboratorActions'
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC'
import { CHANGE_HEADER_LAYOUT, TOGGLE_LAYOUT } from '../../../../../reducer/keys'

export const ThemeItem = ({Index, dataTheme, action, active, themeName, header}) => {
    return (<Col key={Index} className="col-4 p-0 select-grid">
    <Button data-theme={dataTheme} variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100 ${dataTheme == active ? 'active' : ''}`} onClick={action}>
        <div className="w-50px h-50px mb-2">
            <Image className="invert" src={baseUrl(`media/icons/${header ? 'review-header' : 'theme'}/${dataTheme}.svg`)} height={46} width={150} alt={themeName} fluid/>
        </div>
        <p className="mb-0 text-center">{themeName}</p>
    </Button>
</Col>)
}

export const RequestButton = () => {
    return (<Col className="col-4 p-0 select-grid">
    <RequestButtonHOC title={'Request New Themes'}>
        <Button variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100`}>
            <div className="w-50px h-30px mb-2 f-center">
                <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 me-0 text-gray-400'}/>
            </div>
            <p className="mb-0 text-center">Request New Themes</p>
        </Button>
    </RequestButtonHOC>
</Col>)
}

class ThemeList extends Component {
    state = { selectedtheme: false, 
        themeId: this.props.appData?.wall?.Personalization.commerceTheme, 
        isHeader:this.props.appData?.wall?.ThemeRule.showReviewHeader, 
        isWidget:this.props.appData?.wall?.ThemeRule.showReviewList, 
        headerId:this.props.appData?.wall?.ThemeRule.themeHeader
    }

    onClose = () => this.setState({ selectedtheme: false, themeId: null })

    componentDidMount() {
        this.setState({ themeId: this.props.appData?.wall?.Personalization.commerceTheme })
    }
    changeThemes = (themeID, type, wallId) => { console.log(themeID,'themeID')
        this.setState({ selectedtheme: true, themeId: themeID })
        this.props.CHANGE_THEMES(themeID, type, wallId);
    }

    onApplyTheme = () => {
    }

    enableLayout = (type, val) => {
        const stateType = type == 'header' ? 'isHeader' : 'isWidget'
        this.setState({[stateType] : Number(val) }, ()=>this.props.toggleLayout({header:this.state.isHeader, widget:this.state.isWidget}))
    }

    changeHeader = (id) => {
        this.setState({headerId:id}, ()=> this.props.selectHeader(id))
    }


    componentDidUpdate(prevProps) {
        const {appData, sectionChanged, socialReview, selectedContent, resetSection} = this.props
        if (appData.save_and_publish === true && prevProps.appData.save_and_publish !== true) {
            if (appData.is_themes_change) {
                this.props.APPLY_THEME(this.state.themeId, this.onApplyTheme);
            }

        }
        if (appData?.wall?.Personalization.commerceTheme !== prevProps.appData?.wall?.Personalization.commerceTheme) {
            this.setState({ themeId: appData?.wall?.Personalization.commerceTheme })
            this.setState({headerId:appData?.wall?.ThemeRule?.themeHeader, isHeader: appData?.wall?.ThemeRule?.showReviewHeader, isWidget:appData?.wall?.ThemeRule.showReviewList})
        }
        
        if(prevProps.sectionChanged == false && sectionChanged == true){
            this.changeThemes(Number(socialReview) == 1 ? 62 : 63, Number(socialReview), selectedContent?.id)
            resetSection()
        }
    }


    render() {
        const { themeList, appData, socialReview, selectedContent } = this.props
        const { themeId, isHeader, isWidget, headerId } = this.state

        const {themesList, themeHeader} = themeList
        return (<div className='px-4 pb-3 mb-4'>
            {themesList ? 
            <Tab.Container id="socialReviews" activeKey={socialReview}>
                <Tab.Content>
                    <Tab.Pane eventKey="1">
                        <Row className="select-group flex-wrap row gx-8 border-top">
                            {themesList.map((Item, Index) => (
                                Item.themeType !=2 && <ThemeItem Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                            ))}
                            <RequestButton />
                        </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="2">
                        <Row className="select-group flex-wrap row gx-8">
                            <div className="f-between w-100">
                                <Form.Label className="my-4" htmlFor="hlayout_">
                                    <span className="me-2 fs-9">HEADER LAYOUT</span>
                                </Form.Label>
                                <Form.Check type="switch" id="hlayout_" checked={isHeader} onChange={event=> checkPermission('publish', 'website',true) ? this.enableLayout('header',event.target.checked) : {}}/>
                            </div>
                            {themeHeader.map((Item, Index) => ( 
                                <ThemeItem Index={Index} dataTheme={Item.id} active={headerId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeHeader(Item.id) : {}} header={true}/>
                            ))}
                        </Row>
                        <Row className="select-group flex-wrap row gx-8 border-top">
                            <div className="f-between w-100">
                                <Form.Label className="my-4" htmlFor="wlayout_">
                                    <span className="me-2 fs-9">WIDGET LAYOUT</span>
                                </Form.Label>
                                <Form.Check type="switch" id="wlayout_" checked={isWidget} onChange={event => checkPermission('publish', 'website',true) ? this.enableLayout('widget', event.target.checked) : {}} />
                            </div>
                            {themesList.map((Item, Index) => ( 
                                Item.themeType !=1 && <ThemeItem Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                            ))}
                        </Row>
                    </Tab.Pane>
                </Tab.Content>
            
            </Tab.Container> : null }
           
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        appData: state.appData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        CHANGE_THEMES: (id, type, wallKey) => dispatch(CHANGE_THEME_ACTION(id, type, wallKey)),
        APPLY_THEME: (id, callback) => dispatch(APPLY_THEME_ACTION(id, callback)),
        selectHeader: (id) => dispatch({type:CHANGE_HEADER_LAYOUT, payload:id}),
        toggleLayout: (data) => dispatch({type: TOGGLE_LAYOUT, payload:data})
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeList);